body {
    background: rgb(140, 10, 10);
    overflow: hidden;
}

.circle {
    position: absolute;
    border-radius: 50%;
    animation: ripple 20s infinite;
    box-shadow: 0PX 0px 1px 0px;
}

.top {
    top: 10px;
}

.bottom {
    bottom: 10px;
}


.left {
    left: 10px;
}

.right {
    right: 10px;
}


.ripple1 {
    width: 160px;
    height: 160px;
    background: rgba(10, 60, 255, 0.3)
}

.ripple2 {
    width: 200px;
    height: 200px;
    background: rgba(40, 60, 255, 0.3)
}

.ripple3 {
    width: 240px;
    height: 240px;
    background: rgba(80, 60, 255, 0.3)
}


@keyframes ripple{
    0% {transform: scale(1.4)}
    20% {transform: scale(1.9)}
    40% {transform: scale(3.0)}
    60% {transform: scale(2.1)}
    80% {transform: scale(1.5)}
    100% {transform: scale(1.4)}
}

